.wrong-sr
{
    min-height: 100vh;
    min-width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
}
.wrong-sr-container
{
    display: flex;
    flex-direction: column;
    gap: 0;
    align-items: center;
}
.wrong-sr-container h1
{
    margin: 0;
}
.wrong-sr-container p
{
    color: grey;
}
.wrong-sr-container button
{
    box-sizing : border-box; 
    -webkit-tap-highlight-color : rgba(0, 0, 0, 0); 
    color : rgb(255, 255, 255); 
    text-decoration : none solid rgb(255, 255, 255); 
    transition : background 0.2s ease 0s, color 0.2s ease 0s, box-shadow 0.2s ease 0s; 
    font-weight : 400; 
    touch-action : pan-y; 
    -webkit-user-drag : none; 
    user-select : none; 
    position : relative; 
    display : inline-flex; 
    -webkit-box-align : center; 
    align-items : center; 
    -webkit-box-pack : center; 
    justify-content : center; 
    cursor : pointer; 
    padding : 0px 56px; 
    margin : 0px; 
    border-radius : 7px; 
    background-color : rgb(235, 46, 46); 
    border: none;
    outline: none;
    font-size : 16px; 
    line-height : 44.8px; 
    height : 44.9531px; 
    box-shadow : rgba(0, 118, 255, 0.23) 0px 6px 20px 0px; 
}