
.container
{
    border: 2px solid black;
    width: 210mm;
    height: 297mm;
    margin: 0 auto;
}
.container h1
{
    text-align: center;
    font-size: 32px;
    width: 100%;
    border-bottom: 4px solid black;
}
.container h2
{
    text-align: center;
    text-decoration: underline;
    font-size: 26px;
}
.container p
{
    margin-top: 80px;
    font-size: 24px;
    padding: 0 3rem;
    text-align: justify;
    line-height: 32px;
}
.principal-sec
{
    display: flex;
    justify-content: flex-end;
}
.principal
{
    text-align: center;
}

.search-container
{
    width: 100%;
    min-height:100vh ;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
}
.search-container h2
{
    font-size:3rem;
}

.search-container input
{
    font-family : Montserrat, sans-serif; 
    font-size : 17.12px; 
    line-height : 19.688px; 
    margin : 10px 0px; 
    overflow : visible; 
    box-sizing : border-box; 
    padding : 10px 15px; 
    border-radius : 5px; 
    background-color : rgb(234, 234, 234); 
    border : 0px none rgb(0, 0, 0); 
    width : 310.094px; 

}
.search-container .btn
{
    box-sizing : border-box; 
    -webkit-tap-highlight-color : rgba(0, 0, 0, 0); 
    color : rgb(255, 255, 255); 
    text-decoration : none solid rgb(255, 255, 255); 
    transition : background 0.2s ease 0s, color 0.2s ease 0s, box-shadow 0.2s ease 0s; 
    font-weight : 400; 
    touch-action : pan-y; 
    -webkit-user-drag : none; 
    user-select : none; 
    position : relative; 
    display : inline-flex; 
    -webkit-box-align : center; 
    align-items : center; 
    -webkit-box-pack : center; 
    justify-content : center; 
    cursor : pointer; 
    padding : 0px 56px; 
    margin : 0px; 
    border-radius : 7px; 
    background-color : rgb(0, 109, 236); 
    border: none;
    outline: none;
    font-size : 16px; 
    line-height : 44.8px; 
    height : 44.9531px; 
    box-shadow : rgba(0, 118, 255, 0.23) 0px 6px 20px 0px; 
}
.search-container .btn:hover
{
    background-color : rgb(0, 88, 189); 
}

